import React from "react";
import { kFormatter } from "../../../../../../utils";
import { OverlayTrigger, Tooltip } from 'react-bootstrap';
import { FontAwesomeIcon } from "../../../../../CommonComponents/AssetsHelper";
const PercentDifference = (props) => {

    const diff = Math.abs(props.difference)


    return (<OverlayTrigger placement="top" overlay={<Tooltip>% change compared to the last 30 days</Tooltip>}>
        <span className={`score-count text-${props.flag == 1 ? "success" : "danger"} d-flex align-items-center ms-2`} >
            {props.flag == 1 ? (
                <span><FontAwesomeIcon type={'solid'} icon={'caret-up'} iconClass={'fs-6 text-success me-1'} /></span>
            ) : (
                <span><FontAwesomeIcon type={'solid'} icon={'caret-down'} iconClass={'fs-6 text-danger me-1'} /></span>
            )}

            {`${kFormatter(Math.round(diff))}%`}
        </span></OverlayTrigger>
    )
};

export default React.memo(PercentDifference);
