import React, { Suspense } from "react";
import { Card, OverlayTrigger, Tooltip } from "react-bootstrap";
import PercentDifference from "./percentDifference";
import { kFormatter } from "../../../../../../utils";
import { FontAwesomeIcon } from "../../../../../CommonComponents/AssetsHelper";
const TopcardSkeleton = React.lazy(() =>
  import("../skeleton/topcardSkeleton")
);

const TopCard = (props) => {

  const { res, col, loader, data } = props;

  return (
    <>
      {loader ? (
       <Suspense fallback={<></>}> <TopcardSkeleton res={res} col={col} /> </Suspense> 
      ) : data && Object.keys(data).length ? (
        data.map((e, index) => {
          return (
            <div className={`col-sm-6 col-md-6 ${`col-xxl-${res}`}  mb-4`}>
              <Card className={`card-border-top top-border-${index} p-3 card-shadow`}>
                <span className="font-weight-bold font-size-h6 mb-2 text-secondary">
                  {e.name}
                  <OverlayTrigger placement="top" overlay={ <Tooltip>{e.tool_tip ? e.tool_tip : e.name}</Tooltip> } >
                    <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-gray-500 ms-2 fs-6'}/></span>
                  </OverlayTrigger>
                </span>
                <div className="d-flex align-items-center">
                  <span class="font-weight-boldest text-dark fs-3 me-2"> { e.name.includes('Total Posts') ? kFormatter(Math.round(e.count)) : e.count }{ e.name.includes('Recommendation') || e.name.includes('CTR') ? '%' : ''} </span>
                   { e.difference ? <PercentDifference difference={e.difference} flag={e.flag} /> : null }
                </div>
              </Card>
            </div>
          );
        })
      ) : null}
    </>
  );
};

export default TopCard;
